import React, { Component } from 'react';
import InlineWorker from '../src';
import { Row, Col, Card, Icon, Spin, Divider, Button } from 'antd';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/styles/hljs';
import '../style/main.css';
import 'antd/dist/antd.css';

const fn = () => {
	self.onmessage = ({ data }) => {
		var to = setTimeout(() => {
			postMessage('Hello world!');
		}, 1000);
	};
};

export default () => (
	<div>
		<Row>
			<Col span={1}>&nbsp;</Col>
			<Col span={22}>
				<Card
					title={
						<>
							<Icon type="info-circle" theme="twoTone" />
							&nbsp;&nbsp;react-inline-worker
						</>
					}
				>
					<Divider>Example 1</Divider>
					<Row gutter={16}>
						<Col span={12}>
							<Card>1</Card>
						</Col>
						<Col span={12}>
							<Card>
								<InlineWorker
									fn={fn}
									data={{
										var1: 'first variable',
										var2: 'second variable',
									}}
								>
									{({ loading, errors, data }) => {
										if (loading) return <Spin />;
										if (errors) {
											console.error(errors);
											return <div>Errors!</div>;
										}
										return <div>Result from worker: {data}</div>;
									}}
								</InlineWorker>
							</Card>
						</Col>
						<Example
							title="Get a 'Hello world!'"
							description="This is a example"
							code={[
								`import React from 'react';`,
								`import InlineWorker from 'react-inline-worker';`,
								``,
								`function HelloWorldExample(){`,
								`  function fn(){`,
								`    postMessage('Hello world!');`,
								`  }`,
								`  return <InlineWorker fn={fn}>{({ loading, error, data }) => {`,
								`    console.log(data); // Hello world!`,
								`    return false;`,
								`  }}</InlineWorker>;`,
								`}`,
								``,
								`export default HelloWorldExample;`,
							].join('\n')}
							RunComponent={() => {
								function fn() {
									postMessage('Hello world!');
								}
								return (
									<InlineWorker fn={fn}>
										{({ loading, error, data }) => {
											console.log(data); // Hello world!
											return false;
										}}
									</InlineWorker>
								);
							}}
							result="ahaight"
						/>
						<Example
							title="MyExample2"
							description="This is a example"
							code={`var test2 = false;`}
							result="ahaight"
						/>
						<Example
							title="MyExample3"
							description="This is a example"
							code={`var test3 = "myCode";`}
							result="ahaight"
						/>
					</Row>
				</Card>
			</Col>
			<Col span={1}>&nbsp;</Col>
		</Row>
	</div>
);

class Example extends Component {
	constructor(props) {
		super(props);
		this.state = {
			running: false,
		};
	}
	render() {
		const { title, description, code, result, RunComponent } = this.props;
		const { running } = this.state;

		return (
			<div className="example">
				<Divider>{title}</Divider>
				<Row gutter={16}>
					<Col span={18}>
						<Card>
							<small>{description}</small>
							<div className="block">
								<SyntaxHighlighter language="javascript" style={docco}>
									{code}
								</SyntaxHighlighter>
							</div>
						</Card>
					</Col>
					<Col span={6}>
						<Card>
							<Button
								type="primary"
								disabled={running ? true : false}
								onClick={() => this.setState({ running: true })}
							>
								{running ? <Icon type="loading" /> : 'Run'}
							</Button>
							{running ? (
								<div className="block">
									<RunComponent />
								</div>
							) : (
								false
							)}
						</Card>
					</Col>
				</Row>
			</div>
		);
	}
}
